ボタン

汎用的なボタンのスタイルを定義
*/
/*
#styleguide
通常のボタン

汎用的なボタンの雛形を定義

```
<div class="c-btn">詳しくはこちら</div>
```
*/
.c-btn {
  width: 600px;
  height: 80px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.2rem;
  box-shadow: 0 5px 15px 1px rgba(0, 71, 42, 0.35);
  transition: 0.3s;
  cursor: pointer;
  font-family: "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "メイリオ", sans-serif;
  font-weight: bold;
  /*
  #styleguide
  赤バージョン
  ```
  <div class="c-btn c-btn--red">詳しくはこちら</div>
  ```
  */
  /*
  #styleguide
  サイズが小さいバージョン
  ```
  <div class="c-btn c-btn--small">詳しくはこちら</div>
  ```
  */
}
.c-btn::after {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  background-color: rgb(117, 117, 255);
  width: 0;
  height: 100%;
  transition: 0.3s;
  z-index: -1;
}
.c-btn:hover {
  color: #fff;
}
.c-btn:hover::after {
  width: 100%;
}
.c-btn--red::after {
  background-color: rgb(255, 117, 117);
}
.c-btn--small {
  width: 300px;
  height: 50px;
  font-size: 1.8rem;
}

/*
#overview
見出し

汎用的な見出しのスタイルを定義
*/
/*
#styleguide
h2

```
<div class="c-head">ダミーダミーダミーダミー</div>
```
*/
.c-head {
  color: #fff;
  padding: 10px 20px 10px 25px;
  background: linear-gradient(135deg, #545b7d, #000c4a);
  box-shadow: 0 5px 10px rgba(0, 28, 64, 0.25);
  position: relative;
  border-radius: 5px;
  font-size: 2.5rem;
  font-family: "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "メイリオ", sans-serif;
  font-weight: bold;
  /*
  #styleguide
  h3

  ```
  <div class="c-head--h3"><span class="c-head--h3__br"><span>ダミーダミーダミーダミー</div>
  ```
  */
  /*
  #styleguide
  h4

  ```
  <div class="c-head--round">ダミーダミーダミーダミー</div>
  ```
  */
}
@media screen and (max-width: 640px) {
  .c-head {
    font-size: 2.3rem;
    line-height: 1.4;
    padding: 12px 15px 12px 25px;
    border-radius: 3px;
  }
}
.c-head::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 10px;
  width: 5px;
  height: 65%;
  background-color: #fff;
  transform: translateY(-50%);
}
.c-head--h3::after {
  content: "";
  display: block;
  border-bottom: 2px solid #000c4a;
  border-image: linear-gradient(to right, #545b7d, #000c4a);
  border-image-slice: 1;
  box-shadow: 0 5px 10px rgba(0, 88, 124, 0.25);
}
.c-head--h3__br {
  color: #000c4a;
  padding-left: 10px;
  width: calc(100% - 20px);
  margin: 10px;
  position: relative;
  display: inline-block;
  font-size: 2.5rem;
  border-left: 5px solid #949cc1;
  font-family: "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "メイリオ", sans-serif;
  font-weight: bold;
}
@media screen and (max-width: 640px) {
  .c-head--h3__br {
    font-size: 2rem;
    line-height: 1.5;
    padding-left: 20px;
  }
}
.c-head--round {
  position: relative;
  font-size: 2.5rem;
  line-height: 1.4;
  font-family: "游ゴシック体", YuGothic, "游ゴシック Medium", "Yu Gothic Medium", "游ゴシック", "Yu Gothic", "メイリオ", sans-serif;
  font-weight: bold;
}
@media screen and (max-width: 640px) {
  .c-head--round {
    font-size: 2rem;
  }
}
.c-head--round::before {
  content: "●";
  margin-right: 3px;
  display: inline-block;
  font-size: 2.5rem;
  color: #f96352;
}
@media screen and (max-width: 640px) {
  .c-head--round::before {
    font-size: 2rem;
  }
}
.c-head--round__txt {
  font-size: 1.5rem;
  font-family: 游ゴシック medium, yu gothic medium, 游ゴシック, yu gothic, メイリオ, meiryo, ヒラギノ角ゴ pro, hiragino kaku gothic pro, sans-serif;
  font-weight: normal;
}

/*
#overview
ボックス

汎用的なボックスのスタイルを定義
*/
/*
#styleguide
概要
```
<div class="c-box">
  ダミーダミー<br>
  ダミーダミー<br>
  ダミーダミー<br>
  ダミーダミー
</div>
```
*/
.c-box {
  background-color: #fff;
  padding: 20px;
  font-size: 1.6rem;
  line-height: 2;
  display: block;
  width: calc(33.3333333333% - 22px);
  box-shadow: 0 20px 50px rgba(0, 28, 64, 0.25);
  border-radius: 20px;
  transition: 0.3s;
  position: relative;
  top: 0;
  /*
  #styleguide
  ブルーバージョン
  ```
  <div class="c-box c-box--blue">
    ダミーダミー<br>
    ダミーダミー<br>
    ダミーダミー<br>
    ダミーダミー
  </div>
  ```
  */
}
@media screen and (max-width: 1024px) {
  .c-box {
    width: calc(50% - 16px);
  }
}
@media screen and (max-width: 640px) {
  .c-box {
    width: calc(50% - 10px);
    box-shadow: 0 10px 20px rgba(0, 28, 64, 0.25);
    border-radius: 10px;
  }
}
.c-box:hover {
  top: -10px;
}
@media screen and (max-width: 640px) {
  .c-box:hover {
    top: initial;
  }
}
.c-box--blue {
  background-color: #244ad2;
  color: #fff;
}

