}

.u-list_dot li::before {
  content: "・" !important;
  display: block !important;
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
}

.u-list_dot li.dot_none::before {
  display: none !important;
}

.u-list_dot--big li {
  font-size: clamp(14px, 3.5vw, 20px) !important;
}
